import {UploadProps} from "ant-design-vue";
import {fileToJson} from "@/units/xlsx";
import {getNeedLicence} from "@/units/tools";
import {onMounted, ref} from "vue";
import {useLocalData} from "@/units/storage";

const { getLocalData, setLocalData } = useLocalData();

export const useUploadExcel = () => {
    const fileList = ref<UploadProps['fileList']>([]);
    let licences = ref<any[]>([])

    const beforeUpload: UploadProps['beforeUpload'] = file => {
        // @ts-ignore
        fileList.value = [...fileList.value, file];

        fileToJson(file, (data: any) => {
            licences.value = getNeedLicence(data);
            setLocalData(licences.value)
        });

        return false;
    };


    const handleRemove: UploadProps['onRemove'] = file => {
        const index = fileList.value?.indexOf(file);
        const newFileList = fileList.value?.slice();
        // @ts-ignore
        newFileList.splice(index, 1);
        fileList.value = newFileList;
    };

    onMounted(() => {
        const localData: any = getLocalData();
        licences.value = localData || [];
    })

    return {
        beforeUpload,
        handleRemove,
        fileList,
        licences
    }
}