/* eslint-disable */
/**
 * 获取车牌号集合
 */
export function getNeedLicence(data: any) {
    let arr: any[] = []
    data.forEach((wrapArr:any) => {
        wrapArr.list.forEach((cellArr:any) => {
            cellArr.forEach((item:any) => {
                if(typeof item === "string" && item.length === 7){
                    arr.push(item)
                }
            })
        })
    })
    return arr;
}


/**
 * 获取双对称车牌 。 （绝对对称）
 */
export function getDoubleSymmetry(arr:any) {
    let length = arr.length
    const middleIndex = Math.floor(length/2);
    let flag = false;

    for(let i =0; i<= length - 1;  i++){
        if(arr[i] === arr[length -1] && (i < length -1) ){
            if(length -1 - i <= 2){
                flag = true
            }
            length = length -1;
        }else{
            break;
        }
    }

    return flag;
}

/**
 * 获取单对称车牌. 包括了多对称
 */
export function getSoleSymmetry(arr:any) {
    let length = arr.length
    let flag = false;

    for(let i =0; i<= length - 1;  i++){
        if(flag || arr[i] === arr[length -1] && (i < length -1) ){
            flag = true
        }
        length = length -1;
    }

    return flag
}

/**
 * 获取非不吉利数据的车牌
 * @param arr
 * @param badLuckArr
 */
export function getUnBadLuck(arr:any, badLuckArr:any) {
    if(!badLuckArr){
        badLuckArr = [3, 4]
    }
    let flag = true;
    for(let i =0; i<= arr.length - 1;  i++){
        if(badLuckArr.includes(arr[i])){
            flag = false
            break;
        }
    }
    return flag;
}

