/**
 * 三连
 */
export function getThreeRepeat(arr: any) {
    let flag = false;
    for(let i =0; i<= arr.length - 1;  i++){
        if(arr[i] === arr[i+1] && arr[i+1] === arr[i+2]){
            flag = true;
            break;
        }
    }
    return flag
}


/**
 * 获取顺子车牌
 */
export function getStraight(arr:any) {
    arr = arr.map( (i: any)=> i*1)
    let flag = false
    arr = arr.map((i:any) => i*1)
    for(let i =0; i<= arr.length - 3;  i++){
        if((arr[i] === arr[i+1] -1) && (arr[i+1] === arr[i+2]-1) ){
            flag = true
        }else if( (arr[i] === arr[i+1] + 1) && (arr[i+1] === arr[i+2]+1) ){
            flag = true
        }
    }
    return flag;
}


/**
 * 获取顺子车牌（四个）
 */
export function getFour(arr:any) {
    arr = arr.map( (i: any)=> i*1)
    let flag = false
    let count = 0
    arr = arr.map((i:any) => i*1)
    for(let i =0; i<= arr.length - 4;  i++){
        if(arr[i] === arr[i+1] -1){
            if(arr[i+2] === (arr[i+1] + 1) && (arr[i+3] === (arr[i+2] + 1))){
                flag = true
                count+=1
            }
        }else if(arr[i] === arr[i+1] + 1 ){
            if(arr[i+2] === (arr[i+1] - 1) && ( arr[i+3] === (arr[i+2] - 1) )){
                flag = true
                count+=1
            }
        }
    }
    return {
        flag, count
    };
}

/**
 * 获取特殊组合的车牌
 * speStrArr  ['520', "234"]
 */
export function getSpecial(arr:any, speStrArr:any) {
    const str = arr.join("");
    for(let i =0; i<= speStrArr.length - 1;  i++){
        const speStr = speStrArr[i]; // 520
        let flag = false
        if(str.includes(speStr)){
            flag = true
        }
        return flag;
    }
}

