/**
 * 获取(逆向)特殊组合的车牌
 */
export function getRevSpecial(arr:any, speStrArr:any) {
    const str = arr.join("");
    for(let i =0; i<= speStrArr.length - 1;  i++){
        const speStr = speStrArr[i];
        const revStr = speStr.split("").reverse().join("");
        let flag = false
        if(str.includes(revStr)){
            flag = true
        }
        return flag;
    }
}